
.index .side {
  float: left;
  width: 220px;
  border-right: 1px solid #ccc;
  font-size: 16px;
}
.index .side h1 {
  padding: 20px 0;
  border-bottom: 1px solid #ccc;
  text-align: center;
  cursor: pointer;
  font-size: 32px;
  font-weight: bold;
}
.index .side h1 a {
  color: #000;
  text-decoration: none;
}
.index .side h2 {
  margin: 10px 0 0 20px;
}
.index .side ul li {
  margin: 10px 20px;
}
.index .side ul li.active a {
  color: #0e75e1;
}
.index .side ul li a {
  text-decoration: none;
  color: #000;
  display: inline-block;
  border: 1px solid #ccc;
  padding: 10px;
  background: #f0f0f0;
  display: block;
  overflow: hidden;
  text-overflow: ellipsis;
}
.index .content {
  margin-left: 220px;
  padding: 85px 20px 20px;
}
.index .content .demo {
  margin-bottom: 20px;
}
.index .readme blockquote {
  position: absolute;
  top: 10px;
  font-size: 40px;
  border: none;
  color: #0e75e1;
  padding: 0;
}
.index .download {
  margin-bottom: 16px;
}
.index .download a {
  color: #0e75e1;
}



