
.vbl-option {
  height: 28px;
  line-height: 28px;
  padding: 0 8px;
  overflow: hidden;
  color: #000;
}
.vbl-option:hover {
  background: #ebf7ff;
}
.vbl-option.is-selected {
  background: #ebf7ff;
}
.vbl-option.is-disabled {
  background: #f9f9f9;
}
.vbl-option.is-disabled:hover {
  background: #f9f9f9;
}



