
.vbl-checkbox {
  display: inline-block;
  vertical-align: middle;
  white-space: nowrap;
  cursor: pointer;
  outline: none;
  line-height: 1;
  position: relative;
  -webkit-box-sizing: initial;
          box-sizing: initial;
}
.vbl-checkbox + .vbl-checkbox {
  margin-left: 20px;
}
.vbl-checkbox.is-disabled .check {
  background-color: #f9f9f9;
  border-color: #d7d7d7;
}
.vbl-checkbox .control-label {
  margin-left: 6px;
}
.vbl-checkbox .check {
  display: inline-block;
  position: relative;
  top: 3px;
  left: 0;
  width: 12px;
  height: 12px;
  border: 1px solid #dddee1;
  border-radius: 2px;
  background-color: #fff;
  -webkit-transition: border-color 0.2s ease-in-out, background-color 0.2s ease-in-out;
  transition: border-color 0.2s ease-in-out, background-color 0.2s ease-in-out;
}
.vbl-checkbox input[type="checkbox"] {
  display: none;
}
.vbl-checkbox input:checked ~ .control-label {
  color: #1d86f4;
}
.vbl-checkbox input:checked ~ .check {
  background-color: #1d86f4;
  border-color: #1d86f4;
}
.vbl-checkbox input:checked ~ .check:after {
  content: "";
  width: 4px;
  height: 8px;
  position: absolute;
  left: 3px;
  border: 2px solid #fff;
  border-top: 0;
  border-left: 0;
  -webkit-transform: rotate(45deg) scale(0.8);
          transform: rotate(45deg) scale(0.8);
  -webkit-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}



