
.vbl-form-item.is-inline {
  display: inline-block;
}
.vbl-form-item .error-msg {
  display: none;
  margin-top: 4px;
  color: #e9474d;
  line-height: 1;
}
.vbl-form-item.is-error .error-msg {
  display: block;
}
.vbl-form-item.is-error .vbl-select .selected,
.vbl-form-item.is-error .vbl-multi-select .selected,
.vbl-form-item.is-error .vbl-textarea,
.vbl-form-item.is-error .vbl-input input,
.vbl-form-item.is-error .vbl-input .icon-wrap {
  border-color: #e9474d;
}



