
.vbl-page p {
  display: inline-block;
  margin-right: 7px;
  vertical-align: -2px;
}
.vbl-page .per-page {
  margin-right: 10px;
}
.vbl-page .page-prev,
.vbl-page .page-next {
  display: inline-block;
  vertical-align: middle;
  min-width: 28px;
  height: 28px;
  line-height: 27px;
  list-style: none;
  text-align: center;
  cursor: pointer;
  color: #666;
  font-family: Arial;
  border: 1px solid #d7dde4;
  border-radius: 2px;
  -webkit-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}
.vbl-page .page-prev .iconfont,
.vbl-page .page-next .iconfont {
  font-size: 14px;
}
.vbl-page .page-prev:hover {
  border: 1px solid #0e75e1;
}
.vbl-page .page-next.is-disabled:hover,
.vbl-page .page-prev.is-disabled:hover {
  border: 1px solid #d7dde4;
}
.vbl-page .page-next.is-disabled .iconfont,
.vbl-page .page-prev.is-disabled .iconfont {
  color: #eee;
}
.vbl-page .page-next {
  margin-right: 10px;
}
.vbl-page .page-next:hover {
  border: 1px solid #0e75e1;
}
.vbl-page .per-page .vbl-select {
  width: 78px;
  position: relative;
}
.vbl-page .per-page .vbl-select .vbl-btn-primary {
  width: 55px;
  font-size: 12px;
  padding: 0 10px;
}
.vbl-page .per-page .vbl-select .menu {
  position: absolute;
  width: 65px;
  top: 36px;
}
.vbl-page .per-page .vbl-select .menu div > ul {
  width: auto;
  min-width: 75px;
}
.vbl-page .total-num {
  margin-right: 10px;
}
.vbl-page .go-page .vbl-input {
  width: 32px;
  text-align: center;
}
.vbl-page .page-list {
  display: inline-block;
  margin: 0 7px;
  vertical-align: 1px;
}
.vbl-page .page-list li:last-child {
  margin-right: 0;
}
.vbl-page .page-list li {
  display: inline-block;
  vertical-align: middle;
  min-width: 26px;
  height: 26px;
  line-height: 26px;
  margin-right: 4px;
  text-align: center;
  list-style: none;
  cursor: pointer;
  font-family: Arial;
  -webkit-transition: border 0.2s ease-in-out, color 0.2s ease-in-out;
  transition: border 0.2s ease-in-out, color 0.2s ease-in-out;
}
.vbl-page .page-list li a {
  width: 28px;
  height: auto;
  overflow: hidden;
  display: inline-block;
  border: 1px solid #d7dde4;
  border-radius: 4px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.vbl-page .page-list .is-current {
  width: 28px;
  height: auto;
  display: inline-block;
  background: #1d86f4;
  border: 1px solid #1d86f4;
  color: #fff;
}
.vbl-page .page-list li a:hover {
  border: 1px solid #0e75e1;
}
.vbl-page .page-list .page-ellipsis {
  position: relative;
}
.vbl-page .page-list .page-ellipsis:hover {
  border: 1px solid #d7dde4;
}
.vbl-page .page-list .page-ellipsis:hover:after {
  visibility: hidden;
}
.vbl-page .page-list .page-ellipsis:hover .iconfont {
  display: block;
}
.vbl-page .page-list .page-ellipsis:after {
  content: "\2022\2022\2022";
  display: block;
  letter-spacing: 1px;
  color: #ccc;
  text-align: center;
}
.vbl-page .page-list .page-ellipsis .iconfont {
  display: none;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  font-size: 12px;
  color: #0e75e1;
}
.vbl-page .page-list .page-ellipsis .vbl-icon:first-child {
  left: -4px;
}
.vbl-page .page-list .page-ellipsis .vbl-icon:last-child {
  left: 4px;
}



