
.vbl-table {
  overflow-x: auto;
}
.vbl-table table {
  border: 1px solid #d7dde4;
  border-right: 0;
  border-bottom: 0;
  text-align: left;
  line-height: 18px;
}
.vbl-table table a {
  color: #1d86f4;
}
.vbl-table table th,
.vbl-table table td {
  padding: 0 20px;
}
.vbl-table table td {
  padding-top: 10px;
  padding-bottom: 10px;
  word-break: break-all;
}
.vbl-table table .table-sort-wrap span {
  vertical-align: middle;
  margin-right: 4px;
}
.vbl-table table .table-sort-wrap {
  cursor: pointer;
}
.vbl-table table thead {
  display: table-header-group;
  vertical-align: middle;
  border-color: inherit;
  color: #000;
}
.vbl-table table thead tr {
  display: table-row;
  vertical-align: inherit;
  border-color: inherit;
}
.vbl-table table thead tr th {
  min-width: 0;
  height: 36px;
  background: #f5f7f9;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  text-overflow: ellipsis;
  vertical-align: middle;
  border-bottom: 1px solid #e3e8ee;
  border-right: 1px solid #e3e8ee;
  white-space: nowrap;
}
.vbl-table table thead tr th .table-sort {
  display: inline-block;
  width: 9px;
  height: 12px;
  vertical-align: middle;
  overflow: hidden;
  cursor: pointer;
  position: relative;
}
.vbl-table table thead tr th .table-sort.active-up .ion-android-arrow-dropup,
.vbl-table table thead tr th .table-sort.active-down .ion-android-arrow-dropdown {
  color: #1d86f4;
}
.vbl-table table thead tr th .table-sort .vbl-icon {
  display: block;
  height: 6px;
  line-height: 6px;
  overflow: hidden;
  position: absolute;
  color: #bbbec4;
  font-size: 16px;
  -webkit-transition: color 0.2s ease-in-out;
  transition: color 0.2s ease-in-out;
}
.vbl-table table thead tr th .table-sort .vbl-icon:before {
  line-height: 6px;
}
.vbl-table table thead tr th .table-sort .vbl-icon:first-child {
  top: 0;
}
.vbl-table table thead tr th .table-sort .vbl-icon:last-child {
  bottom: 0;
}
.vbl-table table tbody {
  display: table-row-group;
  vertical-align: middle;
  border-color: inherit;
}
.vbl-table table tbody tr {
  display: table-row;
  vertical-align: inherit;
  border-color: inherit;
}
.vbl-table table tbody tr td {
  min-width: 0;
  height: 36px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  text-align: left;
  text-overflow: ellipsis;
  vertical-align: middle;
  border-bottom: 1px solid #e3e8ee;
  border-right: 1px solid #e3e8ee;
}
.vbl-table table tbody tr .more-btn .vbl-icon {
  cursor: pointer;
  font-size: 16px;
}
.vbl-table table tbody tr.row-select,
.vbl-table table tbody tr:hover {
  background: #f2f8ff;
}



