
.vbl-dropdown {
  position: relative;
  display: inline-block;
}
.vbl-dropdown .dropdown-content {
  position: absolute;
  z-index: 999;
  min-width: 100%;
  background: #fff;
  border: 1px solid #eee;
  border-radius: 4px;
  -webkit-box-shadow: 0 1px 20px rgba(0, 0, 0, 0.1);
          box-shadow: 0 1px 20px rgba(0, 0, 0, 0.1);
  padding: 6px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  line-height: 18px;
}



