html {
  box-sizing: border-box;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}
*:before,
*:after {
  box-sizing: inherit;
}
body {
  line-height: 1;
  font-size: 12px;
  color: #333;
  font-family: Helvetica, PingFang SC, Hiragino Sans GB, Microsoft YaHei, Arial, sans-serif;
}
strong,
b {
  font-weight: inherit;
}
em,
i {
  font-style: inherit;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: inherit;
}
img {
  border-style: none;
}
ol,
ul {
  list-style: none;
}
table {
  border-spacing: 0;
}
button,
input,
select,
textarea {
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
}
a,
button {
  cursor: pointer;
  outline: none;
}
textarea {
  overflow: auto;
}
a {
  text-decoration: none;
}
.is-disabled *,
.is-disabled,
[disabled] {
  cursor: not-allowed !important;
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity 150ms;
}
.fade-enter,
.fade-leave-active {
  opacity: 0;
}
@keyframes fadeOutUp {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
    transform: translate3d(0, -100%, 0);
  }
}
.fadeOutUp {
  animation-name: fadeOutUp;
  animation-duration: 150ms;
  animation-fill-mode: both;
}
@keyframes fadeInDown {
  from {
    opacity: 0;
    transform: translate3d(0, -100%, 0);
  }
  to {
    opacity: 1;
    transform: none;
  }
}
.fadeInDown {
  animation-name: fadeInDown;
  animation-duration: 150ms;
  animation-fill-mode: both;
}
.zoom-in-enter-active,
.zoom-in-leave-active {
  transition: opacity 150ms;
}
.zoom-in-enter-active .animation-content,
.zoom-in-leave-active .animation-content,
.zoom-in-enter-active .animation-content,
.zoom-in-leave-active .animation-content {
  transition: transform 150ms;
}
.zoom-in-enter,
.zoom-in-leave-active {
  opacity: 0;
}
.zoom-in-enter .animation-content,
.zoom-in-leave-active .animation-content,
.zoom-in-enter .animation-content,
.zoom-in-leave-active .animation-content {
  transform: scale(0.5);
}



