
.vbl-nav {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  overflow-x: auto;
  white-space: nowrap;
}
.vbl-nav ul {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  border-bottom: 1px solid #1d86f4;
}
.vbl-nav.is-vertical {
  white-space: normal;
}
.vbl-nav.is-vertical ul {
  display: block;
  border-bottom: none;
  border-right: 1px solid #1d86f4;
}
.vbl-nav.is-vertical a {
  margin: 0 -1px 0 0;
  padding: 15px 12px;
}
.vbl-nav.is-vertical a.router-link-exact-active {
  padding: 12px;
  background-color: #fff;
  border-color: #1d86f4;
  border-bottom-color: #1d86f4!important;
  border-right-color: transparent!important;
  color: #1d86f4;
}



