
.vbl-autocomplete {
  position: relative;
}
.vbl-autocomplete .list {
  margin: 8 0;
  width: 100%;
  position: absolute;
  z-index: 999;
  background: #fff;
  margin: 0;
  padding: 8px 0;
  min-width: 96px;
  border: 1px solid #eee;
  border-radius: 4px;
  -webkit-box-shadow: 0 1px 6px rgba(0, 0, 0, 0.2);
          box-shadow: 0 1px 6px rgba(0, 0, 0, 0.2);
}
.vbl-autocomplete .list li {
  height: 28px;
  line-height: 28px;
  padding: 0 8px;
  overflow: hidden;
  color: #000;
}
.vbl-autocomplete .list li:hover {
  background: #ebf7ff;
}
.vbl-autocomplete .list li.is-selected {
  background: #ebf7ff;
}



