
.vbl-multi-select {
  width: 100%;
  background-color: #fff;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  position: relative;
  z-index: 1;
  display: inline-block;
}
.vbl-multi-select .select-mark .vbl-icon {
  position: absolute;
  right: 0;
  top: 50%;
  padding: 8px;
  margin-top: -14px;
  -webkit-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
  line-height: normal;
}
.vbl-multi-select.is-show {
  z-index: 1000;
}
.vbl-multi-select.is-show .select-mark .vbl-icon {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}
.vbl-multi-select.is-disabled .selected {
  background: #f9f9f9;
}
.vbl-multi-select.is-disabled .selected:hover {
  border: 1px solid #e1e1e1;
}
.vbl-multi-select .selected {
  overflow: hidden;
  background: #fff;
  border: 1px solid #e1e1e1;
  color: #000;
  min-height: 26px;
  line-height: 26px;
  padding: 0 24px 0 3px;
  margin: 0;
  border-radius: 4px;
  cursor: pointer;
}
.vbl-multi-select .selected-items {
  overflow: hidden;
  padding-bottom: 3px;
}
.vbl-multi-select .selected:hover {
  border: 1px solid #0e75e1;
}
.vbl-multi-select .selected:active {
  border: 1px solid #0e75e1;
}
.vbl-multi-select .selected-placeholder {
  padding-left: 5px;
}
.vbl-multi-select .selected-item .vbl-icon {
  position: absolute;
  right: 0;
  top: -1px;
  padding: 0 4px;
  color: #666;
  line-height: normal;
}
.vbl-multi-select .selected-item {
  float: left;
  position: relative;
  line-height: 18px;
  margin: 3px 4px 0 0;
  padding: 0 20px 0 8px;
  border: 1px solid #e9eaec;
  border-radius: 3px;
  background: #f7f7f7;
  font-size: 12px;
  vertical-align: middle;
  opacity: 1;
  overflow: hidden;
  cursor: pointer;
}
.vbl-multi-select .list:before {
  content: '';
  border: none;
}
.vbl-multi-select .list:after {
  content: '';
  border: none;
}
.vbl-multi-select .list {
  margin: 8 0;
  width: 100%;
  position: absolute;
  background: #fff;
  margin: 0;
  padding: 8px 0;
  min-width: 96px;
  border: 1px solid #eee;
  border-radius: 4px;
  -webkit-box-shadow: 0 1px 6px rgba(0, 0, 0, 0.2);
          box-shadow: 0 1px 6px rgba(0, 0, 0, 0.2);
}



