
.vbl-multi-option {
  height: 28px;
  line-height: 28px;
  padding: 0 8px;
  overflow: hidden;
  position: relative;
  color: #000;
}
.vbl-multi-option:hover {
  background: #ebf7ff;
}
.vbl-multi-option.is-selected {
  color: #1d86f4;
}
.vbl-multi-option.is-selected .selected-icon {
  display: inherit;
}
.vbl-multi-option.is-disabled {
  background: #f9f9f9;
}
.vbl-multi-option.is-disabled:hover {
  background: #f9f9f9;
}
.vbl-multi-option .selected-icon {
  float: right;
  display: none;
}
.vbl-multi-option .selected-icon:after {
  content: "";
  width: 6px;
  height: 10px;
  position: absolute;
  right: 10px;
  top: 7px;
  border: 2px solid #1d86f4;
  border-top: 0;
  border-left: 0;
  -webkit-transform: rotate(45deg) scale(0.8);
          transform: rotate(45deg) scale(0.8);
  -webkit-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}



