
.vbl-popover {
  display: inline-block;
  position: relative;
}
.vbl-popover.top .arrow {
  bottom: 1px;
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
  left: 50%;
  margin-left: -6px;
}
.vbl-popover.bottom .arrow {
  top: 1px;
  left: 50%;
  margin-left: -6px;
}
.vbl-popover.left .arrow {
  top: 50%;
  right: -2px;
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
  margin-top: -3px;
}
.vbl-popover.right .arrow {
  top: 50%;
  left: -2px;
  -webkit-transform: rotate(-90deg);
          transform: rotate(-90deg);
  margin-top: -3px;
}
.vbl-popover .arrow {
  position: absolute;
  border-left: 6px solid transparent;
  border-right: 6px solid transparent;
  border-bottom: 6px solid #eee;
  content: " ";
  display: block;
  width: 0;
  height: 0;
}
.vbl-popover .arrow:after {
  position: absolute;
  border-left: 5px solid transparent;
  border-right: 5px solid transparent;
  border-bottom: 5px solid #fff;
  content: " ";
  display: block;
  width: 0;
  height: 0;
  top: 1px;
  left: -5px;
}
.vbl-popover .popover-content {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 999;
}
.vbl-popover .popover-main {
  background: #fff;
  border: 1px solid #eee;
  border-radius: 4px;
  -webkit-box-shadow: 0 1px 20px rgba(0, 0, 0, 0.1);
          box-shadow: 0 1px 20px rgba(0, 0, 0, 0.1);
  padding: 6px;
  margin: 6px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  line-height: 18px;
}



