
.vbl-select {
  width: 100%;
  background-color: #fff;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  position: relative;
  z-index: 1;
  display: inline-block;
}
.vbl-select .select-mark .vbl-icon {
  position: absolute;
  right: 0;
  top: 0;
  padding: 8px;
  -webkit-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
  line-height: normal;
}
.vbl-select.is-show {
  z-index: 1000;
}
.vbl-select.is-show .select-mark .vbl-icon {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}
.vbl-select.is-disabled .selected {
  background: #f9f9f9;
}
.vbl-select.is-disabled .selected:hover {
  border: 1px solid #e1e1e1;
}
.vbl-select .selected {
  background: #fff;
  border: 1px solid #e1e1e1;
  color: #000;
  height: 28px;
  line-height: 28px;
  padding: 0 24px 0 8px;
  margin: 0;
  border-radius: 4px;
  cursor: pointer;
}
.vbl-select .selected:hover {
  border: 1px solid #0e75e1;
}
.vbl-select .selected:active {
  border: 1px solid #0e75e1;
}
.vbl-select .list:before {
  content: '';
  border: none;
}
.vbl-select .list:after {
  content: '';
  border: none;
}
.vbl-select .list {
  margin: 8 0;
  width: 100%;
  position: absolute;
  background: #fff;
  margin: 0;
  padding: 8px 0;
  min-width: 96px;
  border: 1px solid #eee;
  border-radius: 4px;
  -webkit-box-shadow: 0 1px 6px rgba(0, 0, 0, 0.2);
          box-shadow: 0 1px 6px rgba(0, 0, 0, 0.2);
}



