
.vbl-modal {
  position: relative;
  z-index: 999;
}
.vbl-modal .mask {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(55, 55, 55, 0.6);
  height: 100%;
  z-index: 1;
}
.vbl-modal .vbl-modal-content-wrap {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100%;
  z-index: 1;
  marign: 0 auto;
  overflow: auto;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: start;
      -ms-flex-align: start;
          align-items: start;
}
.vbl-modal .vbl-modal-content-wrap.vertical-center {
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.vbl-modal .vbl-modal-content-wrap.vertical-center .vbl-modal-content {
  margin: 30px;
}
.vbl-modal .vbl-modal-content-wrap .vbl-modal-content {
  margin: 100px 30px;
  position: relative;
  outline: 0;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid #eee;
  z-index: 1000;
  -webkit-box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
          box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
  border-radius: 4px;
}
.vbl-modal .vbl-modal-content-wrap .vbl-modal-content .vbl-modal-header {
  height: 35px;
  line-height: 1;
  background: #f5f7f9;
}
.vbl-modal .vbl-modal-content-wrap .vbl-modal-content .vbl-modal-header .vbl-modal-close {
  font-size: 20px;
  position: absolute;
  right: 15px;
  top: 2px;
  overflow: hidden;
  cursor: pointer;
  color: #424243;
}
.vbl-modal .vbl-modal-content-wrap .vbl-modal-content .vbl-modal-header .vbl-modal-close .iconfont {
  font-size: 20px;
}
.vbl-modal .vbl-modal-content-wrap .vbl-modal-content .vbl-modal-header .vbl-modal-title {
  display: inline-block;
  width: auto;
  height: auto;
  margin-left: 20px;
  line-height: 36px;
  font-size: 14px;
  font-weight: 700;
  overflow: hidden;
  font-weight: normal;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.vbl-modal .vbl-modal-content-wrap .vbl-modal-content .vbl-modal-body {
  margin: 30px;
}
.vbl-modal .vbl-modal-content-wrap .vbl-modal-content .vbl-modal-footer {
  margin: 0 30px 30px 30px;
  text-align: right;
}
.vbl-modal .vbl-modal-content-wrap .vbl-modal-content .vbl-modal-footer .vbl-button {
  margin-right: 8px;
}
.vbl-modal.is-alert,
.vbl-modal.is-prompt {
  font-size: 14px;
}
.vbl-modal.is-img .vbl-modal-content-wrap.vertical-center .vbl-modal-content {
  margin: 0 60px;
}
.vbl-modal.is-img .vbl-modal-content-wrap .vbl-modal-close {
  font-size: 20px;
  position: absolute;
  right: 20px;
  top: 20px;
  z-index: 999px;
  overflow: hidden;
  cursor: pointer;
  color: #fff;
}
.vbl-modal.is-img .vbl-modal-content-wrap .vbl-modal-content {
  -webkit-box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
          box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
  margin: 20px 60px;
  overflow: hidden;
  border-radius: 0;
  border: none;
}
.vbl-modal.is-img .vbl-modal-content-wrap .vbl-modal-content .vbl-modal-body {
  margin: 0;
}
.vbl-modal.is-img .vbl-modal-content-wrap .vbl-modal-content .vbl-modal-body img {
  width: 100%;
  vertical-align: bottom;
}



