
.vbl-input {
  display: inline-block;
  width: 100%;
}
.vbl-input .input-wrap {
  position: relative;
  display: block;
}
.vbl-input .input-wrap .vbl-icon {
  display: none;
  position: absolute;
  right: 0;
  top: 0;
  padding: 0 6px;
  cursor: pointer;
  color: #666;
}
.vbl-input.has-clear .input-wrap:hover .vbl-icon {
  display: inherit;
}
.vbl-input.has-clear .input-wrap:hover input {
  padding-right: 25px;
}
.vbl-input.has-icon {
  display: table;
}
.vbl-input.has-icon .input-wrap {
  display: table-cell;
}
.vbl-input.has-icon .input-wrap input {
  border-bottom-right-radius: 0;
  border-top-right-radius: 0;
}
.vbl-input.has-icon .icon-wrap {
  display: table-cell;
  width: 1px;
  white-space: nowrap;
  vertical-align: middle;
  padding: 4px 10px;
  background: #f9f9f9;
  border: 1px solid #e1e1e1;
  border-left: none;
  border-bottom-right-radius: 4px;
  border-top-right-radius: 4px;
}
.vbl-input input::-webkit-outer-spin-button,
.vbl-input input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}
.vbl-input input[type="number"] {
  -moz-appearance: textfield;
}
.vbl-input input {
  border: 1px solid #e1e1e1;
  padding: 4px 8px;
  line-height: 1.5;
  color: #000;
  border-radius: 4px;
  width: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-size: 12px;
}
.vbl-input input:focus {
  outline: none;
}
.vbl-input input:hover {
  border: 1px solid #0e75e1;
}
.vbl-input input::-webkit-input-placeholder {
  color: #ababab;
}
.vbl-input input[disabled] {
  border: 1px solid #d7d7d7;
  background-color: #f9f9f9;
}



