
.vbl-radio {
  vertical-align: middle;
  display: inline-block;
  position: relative;
  white-space: nowrap;
  cursor: pointer;
  outline: none;
  -webkit-box-sizing: initial;
          box-sizing: initial;
}
.vbl-radio + .vbl-radio {
  margin-left: 20px;
}
.vbl-radio.is-disabled .check {
  background-color: #f9f9f9;
  border-color: #d7d7d7;
}
.vbl-radio .control-label {
  margin-left: 6px;
}
.vbl-radio .check {
  display: inline-block;
  width: 12px;
  height: 12px;
  position: relative;
  top: 3px;
  left: 0;
  background-color: #fff;
  border: 1px solid #dddee1;
  border-radius: 50%;
  -webkit-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}
.vbl-radio input[type="radio"] {
  display: none;
}
.vbl-radio input:checked ~ .control-label {
  color: #1d86f4;
}
.vbl-radio input:checked ~ .check {
  color: #1d86f4;
  border-color: #1d86f4;
}
.vbl-radio input:checked ~ .check:after {
  position: absolute;
  width: 8px;
  height: 8px;
  left: 2px;
  top: 2px;
  border-radius: 6px;
  display: table;
  border-top: 0;
  border-left: 0;
  content: " ";
  background-color: #2d8cf0;
  opacity: 1;
  -webkit-transform: scale(1);
          transform: scale(1);
  -webkit-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}



