
.vbl-tab-group .tab-title {
  margin-bottom: 20px;
}
.vbl-tab-group .tab-title li {
  display: inline-block;
  padding: 0 20px;
  border-left: 1px solid #e1e1e1;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  cursor: pointer;
  font-size: 14px;
}
.vbl-tab-group .tab-title li:first-child {
  border-left: none;
  padding-left: 0;
}
.vbl-tab-group .tab-title li.is-active {
  color: #1d86f4;
}



