
.vbl-textarea {
  width: 100%;
  height: 76px;
  border: 1px solid #e1e1e1;
  padding: 4px 8px;
  line-height: 1.5;
  color: #000;
  border-radius: 4px;
  font-size: 12px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  vertical-align: bottom;
}
.vbl-textarea:focus {
  outline: none;
}
.vbl-textarea:hover {
  border: 1px solid #0e75e1;
}
.vbl-textarea::-webkit-input-placeholder {
  /* placeholder颜色  */
  color: #ababab;
}
.vbl-textarea[disabled] {
  border: 1px solid #d7d7d7;
  background-color: #f9f9f9;
}



