
.vbl-upload {
  display: inline-block;
}
.vbl-upload .a-upload {
  overflow: hidden;
  position: relative;
  cursor: pointer;
  color: #888;
  display: inline-block;
  *display: inline;
  *zoom: 1;
}
.vbl-upload .a-upload .vbl-button {
  background: transparent;
}
.vbl-upload .a-upload:hover {
  background: #f2f8ff;
}
.vbl-upload .a-upload input {
  position: absolute;
  font-size: 100px;
  right: 0;
  top: 0;
  opacity: 0;
  filter: alpha(opacity=0);
  cursor: pointer;
}
.vbl-upload ul li {
  line-height: 28px;
}



