
.vbl-button {
  border-radius: 2px;
  font-size: 14px;
  height: 28px;
  padding: 0 16px;
  background: #1d86f4;
  color: #fff;
  border: 1px solid #1d86f4;
  cursor: pointer;
  white-space: nowrap;
}
.vbl-button .vbl-icon {
  margin-right: 6px;
}
.vbl-button .vbl-icon,
.vbl-button span {
  vertical-align: middle;
  line-height: normal;
}
.vbl-button:hover {
  background: #0e75e1;
}
.vbl-button.is-large {
  height: 42px;
  border-radius: 4px;
  font-size: 16px;
  text-align: center;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  min-width: 120px;
}
.vbl-button.theme-secondary {
  background: #fff;
  border: 1px solid #d7d7d7;
  color: #333;
}
.vbl-button.theme-secondary:hover {
  background: #f0f0f0;
}
.vbl-button.theme-light-secondary {
  background: #fff;
  border: 1px solid #badcff;
  color: #1d86f4;
}
.vbl-button.theme-light-secondary:hover {
  background: #f2f8ff;
}
.vbl-button.is-disabled {
  background: #f0f0f0;
  border: 1px solid #d5d5d5;
  color: #000;
}
.vbl-button.is-disabled:hover {
  background: #f0f0f0;
  border: 1px solid #d5d5d5;
  color: #000;
}



